.pagination {
  text-align: center;
  margin: 0 auto;
  &__box {
    padding: 10px;
    border: 1px solid $black;
    margin-right: 10px;
    font-size: 22px;
    vertical-align: middle;
    line-height: 50px;
    transition: background .3s;
    &:last-child {
      margin-right: 0;
    }
    &:hover:not(.pagination__box--s) {
      background: $second;
    }
    &--active {
      background: $second;
    }
    &--s {
      border: none;
    }
  }
  &__link {
    display: inline;
  }
}