.header {
  position: fixed;
  display: flex;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 80px;
  z-index: 100;
  background-color: $second-light;
  transition: background-color .5s;
  &--smooth {
    background-color: transparent;
    transition: background-color .5s;
  }
  &--color {
    transition: background-color .5s;
    background-color: $second-light;
  }
  &__wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
  }
  &__logo {
    margin-left: 50px;
  }
}

.logo {
  width: auto;
  text-transform: uppercase;
  &__link {
    display: block;
    color: $black;
  }
  &__img {
    width: 70px;
    padding: 5px 0;
  }
}