.menu {
  font-size: 14px;
  text-transform: uppercase;
  @include tablet {
    display: none;
  }
  &__list {
    display: flex;
    margin-left: 50px;
  }
  &__item {
    margin-right: 20px;
    cursor: pointer;
    transition: color .3s;
    @include mobileL {
      margin-right: 35px;
    }
    &:last-child {
      margin-right: 0;
      margin-left: 40px;
      @include mobileL {
        margin-left: 24px;
      }
    }
  }
  &__link {
    transition: all .3s;
    &:hover {
      color: $accent;
    }
  }
  &-dropdown {
    &:hover &__list {
      display: block;
    }
    &__list {
      display: none;
    }
    &__item {

    }
    &__link {

    }
  }
}

.menu-mobile-btn {
  display: none;
  position: absolute;
  top: 50%;
  right: 2rem;
  font-size: 18px;
  background: transparent;
  text-align: center;
  text-transform: uppercase;
  color: $black;
  transform: translateY(-50%);
  @include tablet {
    display: block;
  }
  @include mobileL {
    font-size: 15px;
  }
}

.menu-mobile {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: $white;
  height: calc(100vh - 80px);
  &__list {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__item {
    margin-bottom: 10px;
  }
  &__link {
  }
}