.feature {
  width: 400px;
  @include tabletL {
    width: 320px;
  }
  @include mobileL {
    width: 240px;
  }
  &__link {
    display: block;
  }
  &__img-container {
    width: 100%;
    height: 400px;
    position: relative;
    background-color: $black;
    overflow: hidden;
    @include tabletL {
      height: 320px;
    }
    @include mobileL {
      height: 240px;
    }
  }
  &__img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
  &__text {
    padding: 50px 0;
    font-size: 15px;
    letter-spacing: 1px;
    background: $white;
    text-align: center;
     @include tabletL {
      padding: 20px 0;
    }
  }
}