html {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  color: $black;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  touch-action: manipulation;
  height: 100%;
  -ms-overflow-style: none;
  scrollbar-width: none;
}

body {
  width: 100%;
  height: 100%;
  font-family: 'Ubuntu', sans-serif;
  font-weight: 400;
  overflow-x: hidden;
}

::selection {
  color: $white;
  background: $accent;
}

h1, h2, h3, h4, h5, h6 {
  text-transform: uppercase;
}

a {
  text-decoration: none;
  color: #000;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
}

.container-inner {
  position: relative;
  max-width: 1120px;
  margin: 0 auto;
  padding: 20px;
}

.container-mini {
  max-width: 900px;
  margin: 0 auto;
}

.spacer {
  height: 80px;
  width: 100%;
}

.btn {
  width: 100%;
  background: transparent;
  text-align: center;
  text-transform: uppercase;
  padding: 20px;
  color: $second;
  border: 2px solid $second;
  transition: background .5s;
  &:hover {
    background: $black;
  }
}