.article {
  text-align: center;
  letter-spacing: 2px;
  line-height: 1.5;
  &__title {
    margin-bottom: 20px;
  }
  &__descr {
    margin-bottom: 30px;
  }
  &__time {
    margin-bottom: 20px;
  }
  &__img {
    width: 500px;
    margin: 0 auto 20px auto;
  }
  &__text {
    margin-bottom: 14px;
    font-size: 18px;
    text-align: left;
  }
  &__footer {
    display: flex;
    padding: 10px 0;
    border: 1px solid $second;
    border-left: none;
    border-right: none;
    @include mobileL {
      flex-direction: column;
    }
    &-left, &-right {
      width: 50%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      @include mobileL {
        width: 100%;
        margin-bottom: 40px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &-social {
      height: 100%;
    }
    &-link {
      display: block;
      transition: color .5s;
      &:hover {
        color: $accent;
      }
    }
  }
}