.footer {
  width: 100%;
  &__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    @include tablet {
      flex-direction: column;
    }
  }
  &__block {
    width: 33.3333%;
    margin: 0 auto;
    @include tablet {
      margin: 0 0 20px 0;
      width: 100%;
    }
  }
  &__title {
    margin-bottom: 20px;
  }
  &__descr {
    font-size: 18px;
    margin-bottom: 30px;
  }
}

.footer-nav {
  &__item {
    margin-bottom: 10px;
  }
}