.home {
  position: relative;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 10;
}

.home-hero {
  position: relative;
  &__bg {
    height: 100vh;
    width: 100%;
    object-fit: cover;
  }
}

.home-offer {
  background: $gradient-bg;
  color: $white;
  &__descr {
    margin: 3em 0;
    text-align: center;
    text-transform: uppercase;
    line-height: 46px;
    font-size: 18px;
    letter-spacing: 2px;
  }
}

.home-features {
  background: $second;
  &__container {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    @include tablet {
      flex-direction: column;
    }
  }
  &__block {
    margin-right: 10px;
    @include tablet {
      margin: 0 auto 20px auto;
    }
    &:hover {
      box-shadow: $shadow-dark;
    }
    &:hover .feature__img {
      transform: scale(1.2);
    }
  }
}