.blog {
  &__title, &__descr {
    text-align: center;
  }
  &__title {
    padding: 20px 0 0 0;
    margin-bottom: 35px;
  }
  &__descr {
    max-width: 400px;
    margin: 0 auto 15px auto;
  }
}

.blog-content {
  &:after {
    content: "\0020";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
}

.blog-article {
  width: calc(50% - 20px);
  padding: 25px 10px 50px 10px;
  border-top: 2px solid $black;
  margin: 10px;
  float: left;
  clear: left;
  &:hover {
    & .blog-article__link {
      & .blog-article__title, .blog-article__read {
        color: $accent;
      }
    }
  }
  @include tabletS {
    width: 100%;
    margin: 0;
  }
  &--right {
    float: right;
    clear: right;
  }
  &--select {
    max-height: 200px;
    text-align: center;
    padding: 40px;
  }
  &__link {
    display: block;
  }
  &__thumb {
    width: 100%;
    height: 0;
    padding-bottom: 140%;
    background-size: cover;
    background-position: center;
    margin-bottom: 10px;
  }
  &__content {
    text-align: center;
  }
  &__time {
    margin-bottom: 10px;
  }
  &__title, &__read {
    margin-bottom: 15px;
    transition: color .3s;
  }
  &__descr {
    margin-bottom: 20px;
  }
}

.blog-select {
  margin: 0 auto;
  text-transform: uppercase;
  padding: 10px 20px;
}