.form {
  &__half {
    padding-right: 5px;
    width: 50%;
    max-width: 50%;
    display: inline-block;
    float: left;
    &:last-of-type {
      padding-right: 0;
    }
  }
  &__input {
    width: 100%;
    padding: 20px 15px;
    border: 2px solid $black;
    margin-bottom: 10px;
  }
}