.products-menu {
  margin-bottom: 20px;
  &__block {
    max-width: 600px;
    margin: 0 auto;
    margin-bottom: 20px;
    & .feature__text {
      background: $second;
      color: $white;
    }
  }
  &__feature {
    width: 100%;
  }
  &__box {
    padding: 20px 0;
    display: flex;
    border-bottom: 2px solid $second;
  }
  &__box-block {
    &:first-child {
      width: 80%;
    }
    &:last-child {
      width: 20%;
      text-align: right;
    }
  }
  &__title {
    font-size: 20px;
  }
  &__price {
    font-size: 18px;
  }
}