.page-header {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url(../img/sec.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  &__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%);
    color: $accent;
    font-size: 38px;
    white-space: nowrap;
    @include tablet {
      font-size: 24px;
    }
    @include mobileL {
      font-size: 18px;
    }
  }
}

.contact-form {
  max-width: 600px;
  margin: 0 auto;
}